.App {
	text-align: center;
}

.App-header {
	background-color: #282c34;
	min-height: 10vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.user-container {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.info-item {
	display: inline;
	width: 15vw;
	height: 4vh;
	padding: 2em;
	border: 2px solid rgb(159, 12, 22);
	align-self: center;
	background-color: #63b7af;
	border-radius: 2em;
	font-size: 20px;
}
